import extend from 'nbd/util/extend';
import View from 'BeFF/View';
import { fabric } from 'fabric';

import template from '../templates/crop-area.mustache';

export default View.extend({
  mustache: template,

  rendered() {
    this._$canvas = this.$view.find('.js-cropper-canvas');
    this._$canvas.prop({
      width: 432,
      height: 300
    });

    this._canvas = new fabric.Canvas(this._$canvas[0]);

    this._createImage();
    this._createCropArea();
    this._createOverlay();
  },

  _createImage() {
    const image = new Image();
    image.onload = () => {
      this._image = new fabric.Image(image, {
        left: 0,
        top: 0,
        originX: 'left',
        originY: 'top',
        hasBorders: false,
        hasControls: false
      });

      this._canvas.add(this._image);
      this._image.sendToBack();
      this._image.scale(1.0);
      this._image.center();

      this._image.on('moving', () => {
        // Some sort of checking should happen here so that we keep the image
        // within the bounds of either the canvas or cropping area
        console.log(`left: ${this._image.get('left')}, top: ${this._image.get('top')}`);
      });
    };
    image.src = '/demo/test-kitten.jpeg';
  },

  _createCropArea() {
    this._cropArea = new fabric.Rect({
      width: 320,
      height: 250,
      fill: 'rgba(255, 255, 255, 0.0)',
      padding: 0,
      borderColor: "#33333",
      selectable: false,
      evented: false,
      hasBorders: true
    });

    this._canvas.add(this._cropArea);
    this._cropArea.center();
    this._cropArea.drawBorders(this._canvas.getContext());
  },

  // -- Points fo figure out where to create
  // -- overlay boxes
  //
  //
  //    x0    x1        x2      x3
  // y0 +------------------------+
  //    |\\\\\\\\\\\\\\\\\\\\\\\\|
  //    |\\\\\\\\\\\\\\\\\\\\\\\\|
  // y1 +------+---------+-------+
  //    |\\\\\\|         |\\\\\\\|
  //    |\\\\\\|    0    |\\\\\\\|
  //    |\\\\\\|         |\\\\\\\|
  // y2 +------+---------+-------+
  //    |\\\\\\\\\\\\\\\\\\\\\\\\|
  //    |\\\\\\\\\\\\\\\\\\\\\\\\|
  // y3 +------------------------+
  //
  _createOverlay() {
    [
      { left: 0, top: 0, height: 25, width: 450 },
      { left: 0, top: 25, height: 250, width: 56 },
      { left: 376, top: 25, height: 250, width: 56 },
      { left: 0, top: 275, height: 25, width: 450 }
    ].forEach((box) => {
      const data = extend(box, {
        fill: 'rgba(37, 38, 42, 0.6)',
        selectable: false,
        evented: false
      });
      this._canvas.add(new fabric.Rect(data));
    });
  }
});



